  showInGameMenu() {
    const menuOptions = [
      {
        label: "Restart level",
        disabled: false,
        selectable: true,
        onSelect: () => this.scene.levels.restart(),
      },
      {
        label: "Exit to menu",
        disabled: false,
        selectable: true,
        onSelect: () => this.scene.scene.start("MenuScene"),
      },
    ];

    this.drawSelectableOptions(null, menuOptions, 0, 4, 1);
  }

  drawSelectableOptions(
    header,
    options,
    initOption,
    offsetX,
    offsetY,
    maxRows = 5
  ) {
    this.options = options;

    const FIRST_SELECTABLE_OPTION = options.findIndex((o) => o.selectable);

    let currentOption = FIRST_SELECTABLE_OPTION;
    if (initOption >= maxRows - 1) {
      // must scroll options
      if (initOption < this.options.length) {
        this.scrollIndex = initOption - maxRows + 1;
        const haveMorePages = this.scrollIndex + maxRows < this.options.length;
        if (haveMorePages) {
          this.scrollIndex = initOption - maxRows + 2;
          // arrow points one before the last
          currentOption = maxRows - 2;
        } else {
          // arrow points the last option
          currentOption = maxRows - 1;
        }
        // and list scrolls up to that option
      } else {
        // latest option, arrow should point to that
        currentOption = maxRows;
        this.scrollIndex = initOption - maxRows;
      }
    } else if (!isNaN(initOption)) {
      currentOption = initOption;
    }

    const currentPage = Math.floor(currentOption / maxRows) + 1;
    let currentIndex = currentOption - Math.floor((currentPage - 1) * maxRows);

    const INIT_POS = currentOption;
    this.ROW_HEIGHT = UI.pixelToScreen(15);
    this.MARGIN_LEFT = UI.tileToScreen(offsetX || 1);
    const ARROW_WIDTH = UI.pixelToScreen(20);
    const OFFSET_X = this.MARGIN_LEFT - ARROW_WIDTH;
    this.MARGIN_TOP = UI.tileToScreen(offsetY || 2);

    const arrow = new Arrow(this.scene).getArrowRight();
    arrow.tilemapLayer.setPosition(
      OFFSET_X,
      this.MARGIN_TOP + this.ROW_HEIGHT * INIT_POS - this.ROW_HEIGHT / 2
    );

    const arrowUp = new Arrow(this.scene).getArrowUp();
    arrowUp.tilemapLayer.setPosition(
      UI.tileToScreen(19),
      this.MARGIN_TOP - this.ROW_HEIGHT
    );
    arrowUp.setAlpha(this.scrollIndex === 0 ? 0.5 : 1);
    arrowUp.setVisible(maxRows < this.options.length);

    const arrowDown = new Arrow(this.scene).getArrowDown();
    arrowDown.tilemapLayer.setPosition(
      UI.tileToScreen(19),
      this.MARGIN_TOP + this.ROW_HEIGHT * maxRows - this.ROW_HEIGHT
    );
    arrowDown.setAlpha(
      this.scrollIndex + maxRows < this.options.length ? 1 : 0.5
    );
    arrowDown.setVisible(maxRows < this.options.length);

    if (header) {
      const label = [
        ...header.label.split(""),
        ...Array(Math.max(0, 20 - header.label.length)).fill(""),
      ].join("")
      const lines = Math.round(label.length / 20)
      const txt = this.scene.add
        .text(
          this.MARGIN_LEFT,
          this.ROW_HEIGHT * -lines + this.MARGIN_TOP,
          label,
          {
            fontFamily: "Emulogic",
            fontSize: "8px",
            fill: "#aee0c8",
            fontWeight: "regular",
          }
        )
        .setVisible(!header.disabled);
      txt.displayHeight = 10 * SCALE;
      txt.alpha = 1;
      txt.depth = 16;
      txt.name = "header_text";
      txt.setScale(SCALE);
      txt.setScrollFactor(0);
      txt.setShadow(-1, 1);
    }

    this.drawOptionsPage(this.scrollIndex, maxRows, currentIndex);

    this.scene.input.keyboard.on("keydown", (event) => {
      // break control for in game pause menu
      if (!this.isVisible) return false;

      const reducedOptions = this.optionsByPage;
      let newIndex = false;
      if (event.key === "ArrowUp") {
        const isNextToFirstOption = currentIndex === 1;
        const scrollOptions = isNextToFirstOption && this.scrollIndex > 0;
        if (scrollOptions) {
          this.scrollIndex--;
          this.drawOptionsPage(this.scrollIndex, maxRows, currentIndex);
        } else {
          // move arrow
          if (currentIndex > 0) {
            newIndex = currentIndex - 1;
          }
        }
      }

      if (event.key === "ArrowDown") {
        const isNextToLastOption = currentIndex === maxRows - 2;
        const scrollOptions =
          isNextToLastOption &&
          this.scrollIndex + maxRows < this.options.length;
        if (scrollOptions) {
          this.scrollIndex++;
          this.drawOptionsPage(this.scrollIndex, maxRows, currentIndex);
        } else {
          // move arrow
          if (currentIndex < reducedOptions.length - 1) {
            newIndex = currentIndex + 1;
          }
        }
      }

      if (
        (event.key === "ArrowDown" || event.key === "ArrowUp") &&
        newIndex !== false
      ) {
        arrow.tilemapLayer.setPosition(
          OFFSET_X,
          this.MARGIN_TOP + this.ROW_HEIGHT * newIndex - this.ROW_HEIGHT / 2
        );
        const prevSelectedOpt = this.scene.children.getByName(
          `menu_text_${currentIndex}`
        );
        prevSelectedOpt.alpha =
          prevSelectedOpt.text.slice(-4) === "100%" ? 1 : 0.5;
        prevSelectedOpt.setColor("#aee0c8");
        const nextSelectedOpt = this.scene.children.getByName(
          `menu_text_${newIndex}`
        );
        nextSelectedOpt.alpha = 1;
        nextSelectedOpt.setColor("#fff");
        currentIndex = newIndex;
      }

      // up down arrows maintenance
      arrowUp.setAlpha(this.scrollIndex === 0 ? 0.5 : 1);
      arrowDown.setAlpha(
        this.scrollIndex + maxRows < this.options.length ? 1 : 0.5
      );

      if (event.key === "Enter") {
        if (!reducedOptions[currentIndex].disabled) {
          reducedOptions[currentIndex].onSelect();
        }
      }
    });
  }

  drawOptionsPage(from, length, current) {
    // clean up
    this.scene.children.list
      .filter((el) => el.name.includes("menu_text"))
      .forEach((el) => el.destroy());
    this.optionsByPage = this.options.slice(from, from + length);

    this.optionsByPage.forEach((o, idx) => {
      const textToPrint = [
        ...o.label.split(""),
        ...Array(Math.max(0, 20 - o.label.length)).fill(""),
      ].join("");
      const txt = this.scene.add
        .text(
          this.MARGIN_LEFT,
          this.ROW_HEIGHT * idx + this.MARGIN_TOP,
          textToPrint,
          {
            fontFamily: "Emulogic",
            fontSize: "8px",
            fill: "#aee0c8",
            fontWeight: "regular",
          }
        )
        .setVisible(!o.disabled);
      txt.alpha = textToPrint.slice(-4) === "100%" || idx === current ? 1 : 0.5;
      txt.setColor(idx === current ? "#fff" : "#aee0c8");
      txt.displayHeight = 10 * SCALE;
      txt.depth = 16;
      txt.name = `menu_text_${idx}`;
      txt.setScale(SCALE);
      txt.setScrollFactor(0);
      txt.setShadow(-1, 1);
    });
  }
