const DB_NAME = 'data'
const DB_INITIAL_STATE = {
  totalShroomsAmount: 0,
  tripDone: false,
  // maxProgress: 0
  // level_1: {
  //   diamonds: 5,
  //   diamonds_picked: 0,
  //   diamonds_to_complete: 4,
  //   enemies: 0,
  //   enemies_killed: 0,
  //   comp: 0,
  // },
}

let memDB = DB_INITIAL_STATE
const bootUp = () => {
  const stored = window.localStorage.getItem(DB_NAME) || ''
  memDB = stored !== '' ? JSON.parse(stored) : DB_INITIAL_STATE
  return memDB
}
const persist = () => window.localStorage.setItem(DB_NAME, JSON.stringify(memDB))

const get = key => eval(`memDB.${key}`)
const set = (key, value) => {
  switch(typeof value) {
    case 'string':
      safeSet(key, `\`${value}\``)
      break
    case 'number':
    case 'boolean':
      safeSet(key, value)
      break
    default:
    case 'object':
      safeSet(key, `JSON.parse(\`${JSON.stringify(value)}\`)`)
      break
  }
  persist()
}
const reset = () => {
  memDB = DB_INITIAL_STATE
  persist()
}

const safeSet = (key, value) => eval(`memDB.${key} = ${value}`)
const getLevels = () => {
  return Object.keys(memDB).map(k => k.includes('level_') ? memDB[k] : null).filter(Boolean)
}
bootUp()
export default {
  get,
  set,
  reset,
  getLevels,
}
