import Phaser from 'phaser'
import { TICK_TIME } from '../utils/constants'
import LevelFinale from './levels/level_finale'

export default class Rules {
  constructor(scene) {
    this.scene = scene
    // gravity loop
    this.timer = 0
    this.worldTickFreq = TICK_TIME
    // scene.events.removeListener(Phaser.Scenes.Events.UPDATE, this.onSceneUpdate)
    scene.events.on(Phaser.Scenes.Events.UPDATE, this.onSceneUpdate.bind(this))

    // this.setUpPuseResumeGame()
    this.setUpCameraFollowPlayer()
    scene.events.off('levelcompleted')
    scene.events.on('levelcompleted', result => {
      scene.events.emit('sounds:level_complete')
      scene.tweens.add({
        targets: this.music,
        volume: 1,
        ease: 'Linear',
        duration: 500,
        onComplete: () => {
          this.music.stop()
        },
      });
      scene.cameras.main.fade(3000, 0, 0)
      scene.cameras.main.once('camerafadeoutcomplete', () => {
        scene.scene.start('LevelResumeScene', { level: scene.levels.current.constructor.key, ...result  })
        // scene.levels.current.cleanLevel()
        // scene.levels.next()
        // scene.cameras.main.fadeIn(1000, 0, 0)
      })
    })
    scene.events.removeListener('levelstart', this.onLevelStart.bind(this))
    scene.events.addListener('levelstart', this.onLevelStart.bind(this))
  }
  destroy() {
    this.music?.stop()
    // this.music.destroy()
    this.scene.events.off(Phaser.Scenes.Events.UPDATE)
    this.scene.events.off('levelstart')
  }
  onLevelStart() {
    if(this.scene.levels.current.id === LevelFinale.key) {
      this.music = this.scene.sound.add('escape_music', { loop: true, valume: 0 })
      
    }else {
      this.music = this.scene.sound.add('levels_surface', { loop: true, valume: 0 })
      // const lvlKey = this.scene.levels.current.id
      // if(lvlKey.includes('level_') || lvlKey.includes('maze_')) {
      // }
    }
    this.music.play()
    // music.volume = 0
    this.scene.tweens.add({  
      targets: this.music,
      volume: 1,
      ease: 'Linear',
      duration: 500,
      onComplete: () => {
      //   titlesMusic.pause()
      //   scene.start(sceneId, props)
      },
    });
  }

  setUpCameraFollowPlayer() {
    this.scene.events.addListener(Phaser.Scenes.Events.UPDATE, () => {
      const cam = this.scene.cameras.main
      const layer = this.scene.children.getByName('layer_1')
      if (!layer) {
        return false
      }
      const { tilemap: map } = layer
      const {x, y} = this.scene.levels.current.getPlayerPos()
      if (cam && map) {
        var smoothFactor = 0.99;
  
        const scrollX = smoothFactor * cam.scrollX + (1 - smoothFactor) * (map.tileToWorldX(x) - cam.width * 0.5);
        const scrollY = smoothFactor * cam.scrollY + (1 - smoothFactor) * (map.tileToWorldY(y) - cam.height * 0.5);

        cam.scrollX = Math.round(scrollX)
        cam.scrollY = Math.round(scrollY)
      }
    })
  }

  onSceneUpdate(time, delta) {
    this.timer += delta
    if (this.timer >= this.worldTickFreq) {
      while (this.timer > this.worldTickFreq) {
          this.timer -= this.worldTickFreq
      }
      this.scene.levels.current.gravityLoop()
    }
  }

  calcCompletion(gems, collectedGems, enemies, killedEnemies, shrooms, colectedShrooms) {
    const levelCompleted = 1
    const comp = (collectedGems + killedEnemies + colectedShrooms + levelCompleted) / (gems + enemies + shrooms + levelCompleted)
    return Math.round(comp * 100) / 100
  }
}
