export const TILES_BY_SCREEN = 20

// incorrectly, I set SCALE as a constant which is set at the moment this const is imported into modules.
// since SCALE is dynamic based on screen size, I need to make it a functions to get called every time we need it.
// it turns out that constant is used in 117 lines by 22 files. To avoid replacing all those occurence, I came out with this hack:
// convert SCALE into a functions and overcharche it method toString so all the places where is used lik num + SCALE invokes
// the calculation of scale each time.
/* proof:
  const SCALE = () => 25
  SCALE() // outputs 25
  SCALE // outputs function
  SCALE.toString = SCALE
  5 + SCALE // outputs 30
*/
export const SCALE_RATIO = window.devicePixelRatio
// export const SCALE = () => SCALE_RATIO

// export const SCALE = () => 1
export const SCALE = () => document.querySelector('canvas').width/TILES_BY_SCREEN/TILE_SIZE
export const SCALE_V = () => document.querySelector('canvas').height/11/TILE_SIZE

// export const SCALE = () => Math.round((document.querySelector('canvas').width/TILES_BY_SCREEN * 100 / TILE_SIZE/100)*100)/100
SCALE.toString = SCALE


