import Explosion from './Explosion'
import Fallable from './Fallable'

export default class Boulder extends Fallable {
  constructor(id, level) {
    super({
      id,
      isRigid: false,
      isBreakable: true,
      isFlat: false,
      isPickable: false,
      isCrushable: false,
      level
    })
    this.strength = 5
  }

  overrunBy(el) {
    if(el.isClass(Explosion)) {
      this.alien.visible = false
      this.alien.destroy()
      this.inactive = true
    }
  }
}
Boulder.className = 'Boulder'
